import React from 'react';
import clsx from 'clsx';
import { Grid, InputLabel, Switch, SwitchProps } from '@material-ui/core';
import { Controller } from 'react-hook-form';
import { createStyles, makeStyles } from '@material-ui/core/styles';
import { Control } from 'react-hook-form/dist/types/form';

interface IProps extends SwitchProps {
  label?: string | JSX.Element;
  labelRight?: string | JSX.Element;
  loading?: boolean;
  name: string;
  error?: boolean;
  viewMode?: boolean;
  marginLeft?: boolean;
  marginRight?: boolean;

  onChangeCustom?: (value: boolean) => void;

  control?: Control;
  helperText?: string;
}

export const AnvSwitch: React.FC<IProps> = ({
  name,
  label,
  control,
  error = false,
  helperText,
  autoFocus,
  className = '',
  labelRight,
  required,
  disabled = false,
  value,
  onChangeCustom,
  loading = false,
  marginLeft = false,
  marginRight = false,
  viewMode = false,
  ...rest
}) => {
  const classes = useStyles();

  const additionalProps: Record<string, any> = {};
  if (typeof value !== 'undefined') {
    additionalProps.value = value;
  }

  const labelComponent = label && (
    <InputLabel
      className={clsx(classes.label, {
        [classes.viewMode]: viewMode,
      })}
      id={`${name}-label`}
      htmlFor={name}
      error={error}
    >
      {label}
    </InputLabel>
  );
  const labelComponentRight = labelRight && (
    <InputLabel
      className={clsx(classes.label, {
        [classes.viewMode]: viewMode,
      })}
      id={`${name}-label`}
      htmlFor={name}
      error={error}
    >
      {labelRight}
    </InputLabel>
  );

  const controlElement = control ? (
    <Controller
      id={name}
      name={name}
      control={control}
      render={(props) => (
        <Switch
          id={name}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
            if (!viewMode) {
              onChangeCustom?.(e.target.checked);
              return props.onChange(e.target.checked);
            }
          }}
          checked={props.value}
          focusVisibleClassName={classes.focusVisible}
          disableRipple
          classes={{
            root: classes.root,
            switchBase: clsx(classes.switchBase, {
              [classes.viewMode]: viewMode,
            }),
            thumb: classes.thumb,
            track: classes.track,
            checked: classes.checked,
          }}
          {...rest}
        />
      )}
    />
  ) : (
    <Switch
      id={name}
      checked={!!value}
      focusVisibleClassName={classes.focusVisible}
      disableRipple
      classes={{
        root: classes.root,
        switchBase: clsx(classes.switchBase, { [classes.viewMode]: viewMode }),
        thumb: classes.thumb,
        track: classes.track,
        checked: classes.checked,
      }}
      {...rest}
    />
  );

  return (
    <>
      <Grid
        component="label"
        container
        alignItems="center"
        spacing={1}
        className={clsx(classes.wrapper, {
          [classes.marginLeft]: marginLeft,
          [classes.marginRight]: marginRight,
        })}
      >
        {labelComponent && <Grid item>{labelComponent}</Grid>}

        <Grid item>{controlElement}</Grid>

        {labelComponentRight && <Grid item>{labelComponentRight}</Grid>}
      </Grid>
    </>
  );
};

export const useStyles = makeStyles((theme) =>
  createStyles({
    wrapper: {
      display: 'inline-flex',
      width: 'auto',
    },
    label: {
      color: theme.palette.text.body,
      fontSize: 13,
      lineHeight: '22px',

      cursor: 'pointer',
    },

    marginLeft: {
      marginLeft: theme.spacing(2),
    },
    marginRight: {
      marginRight: theme.spacing(2),
    },

    root: {
      width: 34,
      height: 20,
      padding: 0,
      margin: 2,
    },
    viewMode: {
      cursor: 'default',
    },
    switchBase: {
      padding: 1,
      '&$checked': {
        transform: 'translateX(16px)',
        color: theme.palette.common.white,

        '& + $track': {
          backgroundColor: theme.palette.brand.main,
          opacity: 1,
          border: 'none',
        },
      },
      '&$focusVisible $thumb': {
        color: theme.palette.brand.main,
        border: '6px solid #fff',
      },
    },
    thumb: {
      marginTop: 1,
      width: 16,
      height: 16,
      background: '#FFFFFF',
      boxShadow:
        '0px 4px 8px rgba(0, 0, 0, 0.08), 0px 0px 2px rgba(0, 0, 0, 0.12), 0px 0px 1px rgba(0, 0, 0, 0.08)',
    },
    track: {
      borderRadius: 12,
      border: 'unset',
      // border: `1px solid ${theme.palette.grey[400]}`,
      backgroundColor: theme.palette.grey[200],
      opacity: 1,
      transition: theme.transitions.create(['background-color', 'border']),
    },
    checked: {},
    focusVisible: {},
  }),
);

export default AnvSwitch;
