import React from 'react';
import clsx from 'clsx';
import {
  FormControl,
  FormHelperText,
  InputLabel,
  MenuItem,
  Select,
} from '@material-ui/core';
import { Controller } from 'react-hook-form';
import { createStyles, makeStyles } from '@material-ui/core/styles';
import { SelectProps } from '@material-ui/core/Select';
import { Control } from 'react-hook-form/dist/types/form';

export interface ISelectOption {
  label: string | JSX.Element;
  value: string | number;
  rightCaption?: string | React.ReactNode | JSX.Element;
  bottomCaption?: string | JSX.Element;
  disabled?: boolean;
  type?: string;

  active?: boolean;
}

interface IProps extends SelectProps {
  loading?: boolean;
  name: string;
  control?: Control;
  helperText?: string | JSX.Element;
  options: ISelectOption[];
  handleSelectMenuItem?: (arg: string | number) => void;
}

export const AnvSelect: React.FC<IProps> = ({
  name,
  label,
  control,
  error,
  helperText,
  autoFocus,
  className = '',
  options,
  required,
  disabled = false,
  value,
  handleSelectMenuItem = () => {},
  loading = false,
  ...rest
}) => {
  const classes = useStyles();

  const additionalProps: Record<string, any> = {};
  if (typeof value !== 'undefined') {
    additionalProps.value = value;
  }

  const inputEl = React.useRef<HTMLInputElement>(null);
  React.useEffect(() => {
    if (autoFocus && inputEl?.current?.scrollIntoView) {
      inputEl?.current?.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
  }, [inputEl, autoFocus]);

  const select = (
    <Select
      innerRef={inputEl}
      labelId={`${name}-label`}
      name={name}
      required={required}
      disabled={disabled}
      MenuProps={{
        anchorOrigin: {
          vertical: 'bottom',
          horizontal: 'left',
        },
        transformOrigin: {
          vertical: 'top',
          horizontal: 'left',
        },
        getContentAnchorEl: null,
        classes: {
          paper: classes.selectPaper,
        },
      }}
      inputProps={{ id: name }}
      className={classes.selectRoot}
      {...rest}
      {...additionalProps}
    >
      {rest?.displayEmpty && <MenuItem value="" />}

      {options.map((item) => (
        <MenuItem
          disabled={!!item?.disabled}
          className={classes.item}
          key={`${item.value}-${item.label}`}
          value={item.value}
          onClick={() => handleSelectMenuItem(item.value)}
        >
          {item.label}
          {item.rightCaption && (
            <span className={classes.caption}>{item.rightCaption}</span>
          )}
        </MenuItem>
      ))}
    </Select>
  );

  return (
    <FormControl
      variant="filled"
      className={clsx(className)}
      fullWidth
      required={required}
      error={!!error}
      disabled={disabled}
    >
      <InputLabel id={`${name}-label`} htmlFor={name}>
        {label}
      </InputLabel>

      {control ? (
        <Controller name={name} control={control} as={select} />
      ) : (
        select
      )}

      {!!helperText && (
        <FormHelperText component={'span'}>{helperText}</FormHelperText>
      )}
    </FormControl>
  );
};

const useStyles = makeStyles((theme) =>
  createStyles({
    selectPaper: {
      marginTop: theme.spacing(0.5),
      borderRadius: 12,
    },
    selectRoot: {
      '&& .MuiSelect-root': {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        paddingRight: theme.spacing(4),
      },
    },
    item: {
      display: 'flex',
      justifyContent: 'space-between',
      fontSize: 14,
      lineHeight: '24px',
      color: theme.palette.text.secondary,
    },
    caption: {
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(1),
      fontWeight: 'bold',
    },
  })
);
