import React from 'react';
import { FormattedMessage } from 'react-intl';

import { Grid } from '@material-ui/core';

import { Pagination, PaginationItem } from '@material-ui/lab';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';

import { IPagination } from 'src/stores/_helpers/interfaces';

import AnvButton from 'src/components/shared/anv-button';

interface IProps {
  loading?: boolean;
  pagination: IPagination;
  totalPages: number;
  page?: number;
  handleChangePage?: (ev: React.ChangeEvent<unknown>, value: number) => void;
}
export const AnvPaginationMui: React.FC<IProps> = ({
  loading = false,
  pagination,
  page = 1,
  totalPages,
  handleChangePage,
}) => {
  if (!totalPages || totalPages < 2) {
    return null;
  }

  return (
    <Grid container justify={'center'} style={{ marginTop: 40 }}>
      <Grid item>
        <Pagination
          disabled={loading}
          count={pagination.totalPages}
          page={+page}
          onChange={handleChangePage}
          variant="outlined"
          color="primary"
          renderItem={(item) => {
            if (item.type === 'previous') {
              return (
                <AnvButton
                  size={'small'}
                  style={{ marginRight: 16 }}
                  disabled={item.disabled}
                  onClick={item.onClick}
                  startIcon={<ArrowBackIosIcon fontSize={'small'} />}
                  color={'primary'}
                >
                  <FormattedMessage id="APP.BACK" defaultMessage="Back" />
                </AnvButton>
              );
            }
            if (item.type === 'next') {
              return (
                <AnvButton
                  size={'small'}
                  style={{ marginLeft: 16 }}
                  endIcon={<ArrowForwardIosIcon fontSize={'small'} />}
                  disabled={item.disabled}
                  onClick={item.onClick}
                  color={'primary'}
                >
                  <FormattedMessage id="APP.NEXT" defaultMessage="Next" />
                </AnvButton>
              );
            }

            return <PaginationItem {...item} />;
          }}
        />
      </Grid>
    </Grid>
  );
};
