export default ModuleRequestScopeSchool;

const prepareSchoolAddressCaption = ({
  address,
  locale,
  classes,
}: any): string | JSX.Element => {
  const { stateRef: state, county, city } = address;
  const addressArr: string[] = [];
  const countyName = getNameByLangNullable(county, 'name', locale);
  const cityName = getNameByLangNullable(city, 'name', locale);

  if (state.code) {
    addressArr.push(state.code);
  }
  if (countyName) {
    addressArr.push(countyName);
  }
  if (cityName) {
    addressArr.push(cityName);
  }

  return (
    <span className={classes.schoolAddressCaption}>
      {addressArr.join(', ')}
    </span>
  );
};
