import React from 'react';
import { Paper } from '@material-ui/core';
import { FormattedMessage } from 'react-intl';
import { createStyles, makeStyles } from '@material-ui/core/styles';

import { AUTOCOMPLETE_SERVER_LIMIT } from 'src/constants';

interface IProps {
  // total: number;
}

export const AutocompleteTotalContext = React.createContext<number | undefined>(
  0,
);

export const PaperTotalAutocomplete: React.FC<IProps> = ({ children }) => {
  const total = React.useContext(AutocompleteTotalContext);
  const classes = useStyles();

  return (
    <Paper elevation={1} className={classes.autocompletePaper}>
      {children}

      {!!total && total > AUTOCOMPLETE_SERVER_LIMIT && (
        <div className={classes.totalWrapper}>
          <FormattedMessage
            id="APP.COMMON.AUTOCOMPLETE.TOTAL"
            defaultMessage="Found {total} records, type to make quick search"
            values={{
              total,
            }}
          />
        </div>
      )}
    </Paper>
  );
};

export const useStyles = makeStyles((theme) =>
  createStyles({
    totalWrapper: {
      padding: theme.spacing(0, 2, 1),

      color: theme.palette.text.disabled,
      fontSize: 12,
    },
    autocompletePaper: {
      overflow: 'hidden',
      fontSize: '1rem',
      fontWeight: 400,
      lineHeight: 1.5,
      letterSpacing: '0.00938em',
    },
  }),
);
