import React, { useEffect } from 'react';
import clsx from 'clsx';

import { observer } from 'mobx-react-lite';
import { FormattedMessage, useIntl } from 'react-intl';
import { useFormContext, useWatch } from 'react-hook-form';
import { capitalize, Divider, Grid } from '@material-ui/core';

import { IRequestAccessModuleScope } from 'src/models/requests';

import { AnvSelect } from 'src/components/shared/form-controls/anv-select';
import { AnvAutocomplete2 } from 'src/components/shared/form-controls/anv-autocomplete-2';

import {
  formErrorHelper,
  getItemById,
  getNameByLangNullable,
  prepareOptions,
} from 'src/utils';
import {
  ACCESS_SCOPES,
  AUTOCOMPLETE_HISTORY_LIMIT,
  ORGANIZATION_TYPE_CODES,
} from 'src/constants';
import { usePrevious } from 'src/hooks';
import { defaultMetaStore, ScopeFormContext } from '../context';
import { useStyles } from '../styles';
import { useModal } from '../../../utils/hooks';
import { GroupHeaderAutocomplete } from '../../shared/form-controls/anv-autocomplete-2/parts/GroupHeader';
import { AnvButton } from '../../shared/anv-button';
import { AnvAccessHistoryModal } from '../history-modal';

interface IProps {
  jurisdictions?: any;
}

const ModuleRequestScopeAuthorizer: React.FC<IProps> = observer(
  ({ jurisdictions }) => {
    const classes = useStyles();
    const { locale } = useIntl();
    const { control, errors } = useFormContext();

    const {
      onlyMyAccessByModule,
      metaStores = defaultMetaStore,
      serverErrors = {},
    } = React.useContext(ScopeFormContext) || {};

    const {
      organizationsStore: {
        total,
        getOrganizations,
        data: organizations,
        isFetching: isFetchingOrganizations,
      },
      accessHistoryStore: { data: accessHistory = [] },
    } = metaStores;

    const scope: IRequestAccessModuleScope = useWatch({
      control,
      name: 'scope',
      defaultValue: {},
    });
    const accessScope = useWatch({
      control,
      name: 'accessScope',
      defaultValue: {},
    });

    const { jurisdiction, organization } = scope || {};

    const [orgInputValue, setOrgInputValue] = React.useState('');
    const orgInputValuePrev = usePrevious(orgInputValue);

    const entitiesAll = React.useMemo(() => {
      const array: Record<string, any>[] = [];

      if (organizations) {
        array.push(...(organizations || []));
      }

      if (accessHistory) {
        array.push(
          ...(accessHistory
            .map(
              (historyItem: Record<string, any>) => historyItem?.organizationRef
            )
            .filter((item: any) => !!item) || [])
        );
      }

      return array;
    }, [organizations, accessHistory]);

    const organizationEntity = getItemById(entitiesAll, organization);
    const organizationEntityName = getNameByLangNullable(
      organizationEntity,
      'name',
      locale
    );

    const jurisdictionPrev = usePrevious(jurisdiction);

    useEffect(() => {
      if (
        jurisdiction !== jurisdictionPrev ||
        (orgInputValuePrev !== orgInputValue &&
          organizationEntityName !== orgInputValue)
      ) {
        const data: Record<string, any> = {
          getWithTotal: true,
          filter: {
            accessScope: { eq: ORGANIZATION_TYPE_CODES.AUTHORIZER },
          },
        };

        if (jurisdiction) {
          data.filter.or = [
            {
              jurisdiction: {
                eq: jurisdiction,
              },
            },
            {
              jurisdiction: {
                is: null,
              },
            },
          ];
        }

        if (orgInputValue) {
          data.name = orgInputValue;
          data.locale = locale;
        }

        if (onlyMyAccessByModule) {
          data.filterAccess = {};

          data.filterAccess.accessScope = ACCESS_SCOPES.AUTHORIZER;
          data.filterAccess.moduleCode = onlyMyAccessByModule;
        }

        getOrganizations(data);
      }
    }, [
      organizationEntityName,
      orgInputValuePrev,
      orgInputValue,
      jurisdiction,
      jurisdictionPrev,
      locale,
      accessScope,
      onlyMyAccessByModule,
      getOrganizations,
    ]);

    const jurisdictionsOptions = prepareOptions(jurisdictions, {
      locale,
    });
    // const organizationsOptions = organizations
    //   ? prepareOptions(organizations, {
    //       locale,
    //     })
    //   : [];

    const organizationsOptionsActual = isFetchingOrganizations
      ? []
      : prepareOptions(organizations, {
          locale,
          type: 'organization',
        });
    const optionsHistory = prepareOptions(
      accessHistory?.filter(
        (item: any) => item?.active && item?.organizationRef
      ),
      {
        locale,
        labelFunc: (item) => {
          return getNameByLangNullable(item?.organizationRef, 'name', locale);
        },
        customId: 'organization',
        type: 'history',
      }
    );

    const { handleToggleModal: toggleHistoryViewAll, isOpen: historyViewAll } =
      useModal();

    const totalHistory = optionsHistory?.length || 0;
    const historyLengthToShow = historyViewAll
      ? optionsHistory.length
      : AUTOCOMPLETE_HISTORY_LIMIT;

    const organizationsOptions = React.useMemo(() => {
      const historyFiltered =
        optionsHistory
          ?.filter((item: Record<any, any>) => !!item.active)
          .slice(0, historyLengthToShow) || [];

      return [...historyFiltered, ...organizationsOptionsActual];
    }, [optionsHistory, organizationsOptionsActual, historyLengthToShow]);

    const { handleToggleModal: handleToggleHistory, isOpen: historyOpen } =
      useModal();

    //  AUTHORIZER: [
    //         'jurisdiction',
    //         'autocomplete  - organization type Authorizer type (3)',
    //       ],

    const formHelper = (field: string) => {
      return formErrorHelper(field, errors, serverErrors);
    };

    return (
      <>
        <AnvSelect
          displayEmpty
          name={'scope.jurisdiction'}
          control={control}
          label={
            <FormattedMessage
              id="APP.COMMON.LABEL.JURISDICTION"
              defaultMessage="Jurisdiction"
            />
          }
          options={jurisdictionsOptions}
          helperText={formHelper('scope.jurisdiction')}
          error={!!formHelper('scope.jurisdiction')}
        />

        <AnvAutocomplete2
          total={total}
          required
          options={organizationsOptions}
          loading={isFetchingOrganizations}
          label={
            <FormattedMessage
              id="APP.COMMON.LABEL.ORGANIZATION"
              defaultMessage="Organization"
            />
          }
          onInputChange={(event, newInputValue) => {
            setOrgInputValue(newInputValue);
          }}
          control={control}
          name={'scope.organization'}
          helperText={formHelper('scope.organization')}
          error={!!formHelper('scope.organization')}
          groupBy={(option) => option.type || ''}
          renderGroup={(group) => {
            let groupLocaleKey = group.group.toUpperCase();

            const isHistory = group.group === 'history';

            return (
              <Grid
                key={group.key || Math.random()}
                className={clsx({
                  [classes.historyGroup]: isHistory,
                })}
              >
                <GroupHeaderAutocomplete
                  title={
                    <FormattedMessage
                      id={`APP.COMMON.FILTER.DROPDOWN.${groupLocaleKey}`}
                      defaultMessage={capitalize(group.group)}
                    />
                  }
                >
                  {isHistory && (
                    <AnvButton
                      className={classes.autocompleteHeaderButton}
                      variant={'text'}
                      color={'primary'}
                      onClick={handleToggleHistory}
                    >
                      <FormattedMessage
                        id="APP.COMMON.FILTER.ARCHIVE"
                        defaultMessage="Archive"
                      />
                    </AnvButton>
                  )}
                </GroupHeaderAutocomplete>

                <Divider />

                {group.children}

                {isHistory && totalHistory > AUTOCOMPLETE_HISTORY_LIMIT && (
                  <div
                    className={clsx(
                      classes.wrapper,
                      classes.autocompleteFooter
                    )}
                  >
                    <AnvButton
                      variant={'text'}
                      color={'primary'}
                      className={classes.autocompleteFooterButton}
                      onClick={toggleHistoryViewAll}
                    >
                      {historyViewAll ? (
                        <FormattedMessage
                          id="APP.COMMON.FILTER.HIDE_ALL"
                          defaultMessage="Hide"
                        />
                      ) : (
                        <FormattedMessage
                          id="APP.COMMON.FILTER.VIEW_ALL"
                          defaultMessage="View all"
                        />
                      )}
                    </AnvButton>
                  </div>
                )}
              </Grid>
            );
          }}
        />

        <AnvAccessHistoryModal
          handleToggle={handleToggleHistory}
          open={historyOpen}
        />
      </>
    );
  }
);

export default ModuleRequestScopeAuthorizer;
