import React from 'react';

import { observer } from 'mobx-react-lite';
import { FormattedMessage, useIntl } from 'react-intl';
import { useFormContext, useWatch } from 'react-hook-form';

import { IRequestAccessModuleScope } from 'src/models/requests';

import { AnvSelect } from 'src/components/shared/form-controls/anv-select';
import { AnvAutocomplete2 } from 'src/components/shared/form-controls/anv-autocomplete-2';

import {
  formErrorHelper,
  getItemById,
  getNameByLangNullable,
  prepareOptions,
} from 'src/utils';
import { ACCESS_SCOPES, ORGANIZATION_TYPE_CODES } from 'src/constants';

import { defaultMetaStore, ScopeFormContext } from '../index';

import { usePrevious } from 'src/hooks';

interface IProps {
  active?: boolean;
  jurisdictions?: any;
}

const ModuleScopeFilterOrganization: React.FC<IProps> = observer(
  ({ active, jurisdictions }) => {
    const { control, errors, setValue } = useFormContext();
    const { locale } = useIntl();

    const {
      existsData: item,
      onlyMyAccessByModule,
      particularTool,
      metaStores = defaultMetaStore,
      serverErrors = {},
      organizationRequired,
    } = React.useContext(ScopeFormContext) || {};
    const {
      organizationTypesStore: { getOrganizationTypes, data: organizationTypes },
      organizationsStore: {
        getOrganizations,
        getOrganization,
        organizationsMap,
        data: organizations,
        data2: organizations2,
        data3: organizations3,
        total: organizationsTotal,
        total2: organizationsTotal2,
        total3: organizationsTotal3,
        isFetching: isFetchingOrganizations,
      },
      accessHistoryStore: { data: accessHistory = [] },
    } = metaStores;

    const [initialOrganization] = React.useState(item?.scope?.organization);
    // const [initialOrganization] = React.useState(item?.scope?.organization);

    React.useEffect(() => {
      if (initialOrganization) {
        getOrganization({ id: initialOrganization });
      }
    }, [initialOrganization, getOrganization]);

    // React.useEffect(() => {
    //   if (getAccessScopes) {
    //     const filterAccess: Record<string, any> = {};
    //
    //     if (onlyMyAccessByModule) {
    //       filterAccess.moduleCode = onlyMyAccessByModule;
    //       filterAccess.toolCode = particularTool;
    //     }
    //
    //     getAccessScopes({ filterAccess });
    //   }
    // }, [accessScope, particularTool, onlyMyAccessByModule, getAccessScopes]);

    React.useEffect(() => {
      const filterAccess: Record<string, any> = {};

      if (onlyMyAccessByModule) {
        filterAccess.moduleCode = onlyMyAccessByModule;
        filterAccess.toolCode = particularTool;
      }

      getOrganizationTypes({ filterAccess });
    }, [onlyMyAccessByModule, particularTool, getOrganizationTypes]);

    const entitiesAll = React.useMemo(() => {
      const array: Record<string, any>[] = [];

      if (organizations) {
        array.push(...(organizations || []));
      }

      if (accessHistory) {
        array.push(
          ...(accessHistory
            .map(
              (historyItem: Record<string, any>) => historyItem?.organizationRef
            )
            .filter((item: any) => !!item) || [])
        );
      }

      return array;
    }, [organizations, accessHistory]);

    const [orgInputValue, setOrgInputValue] = React.useState('');
    const [org2InputValue, setOrg2InputValue] = React.useState('');
    const [org3InputValue, setOrg3InputValue] = React.useState('');

    const accessScope: string = useWatch({
      control,
      name: 'accessScope',
      defaultValue: item?.accessScope || '',
    });

    const scope: IRequestAccessModuleScope = useWatch({
      control,
      name: 'scope',
      defaultValue: item?.scope || {},
    });

    const {
      jurisdiction,
      orgType,
      organization,
      organizationSecondary,
      organizationThird,
    } = scope;

    const organizationStore =
      organization &&
      (organizationsMap?.[organization] ||
        entitiesAll?.find((item) => item._id === organization));

    /** ORG */
    const organizationTypeEntity = getItemById(organizationTypes, orgType);
    const orgAccessScope = organizationTypeEntity?.accessScope;

    const organizationEntity = getItemById(entitiesAll, organization);

    const organizationEntityName = getNameByLangNullable(
      organizationEntity,
      'name',
      locale
    );

    const organizationSecondaryEntity = getItemById(
      organizations2,
      organizationSecondary
    );
    const organizationSecondaryEntityName = getNameByLangNullable(
      organizationSecondaryEntity,
      'name',
      locale
    );

    const organizationThirdEntity = getItemById(
      organizations3,
      organizationThird
    );
    const organizationThirdEntityName = getNameByLangNullable(
      organizationThirdEntity,
      'name',
      locale
    );

    React.useEffect(() => {
      if (active && accessScope !== orgAccessScope) {
        setValue('accessScope', orgAccessScope || null);
      }
    }, [accessScope, orgAccessScope, active, setValue]);

    const organizationEntityJurId = organizationEntity?.jurisdiction?._id;

    const orgInputValuePrev = usePrevious(orgInputValue);
    const org2InputValuePrev = usePrevious(org2InputValue);
    const org3InputValuePrev = usePrevious(org3InputValue);

    const jurisdictionPrev = usePrevious(jurisdiction);
    const orgTypePrev = usePrevious(orgType);

    const organizationSecondaryPrev = usePrevious(organizationSecondary);
    const organizationThirdPrev = usePrevious(organizationThird);

    const jurisdictionChanged =
      item?.jurisdiction !== jurisdiction && jurisdiction !== jurisdictionPrev;

    // JUR or Type changed
    React.useEffect(() => {
      if (active && accessScope !== ACCESS_SCOPES.SCHOOL) {
        /** Type changed */
        if (
          orgType &&
          orgTypePrev &&
          orgTypePrev !== orgType &&
          organizationEntity?.organizationType?._id !== orgType
        ) {
          setValue('scope.jurisdiction', null);
          setValue('scope.organization', null);
          setValue('scope.organizationSecondary', null);
          setValue('scope.organizationThird', null);

          return;
        }

        /** Jur changed */
        if (
          jurisdictionChanged &&
          jurisdictionPrev &&
          organizationEntity?.organizationType?.jurisdiction !== jurisdiction
        ) {
          setValue('scope.organization', null);
          setValue('scope.organizationSecondary', null);
          setValue('scope.organizationThird', null);
        }
      }
    }, [
      active,
      organizationEntity,
      accessScope,
      orgType,
      orgTypePrev,
      jurisdiction,
      jurisdictionChanged,
      jurisdictionPrev,
      setValue,
    ]);

    React.useEffect(() => {
      if (organizationEntity) {
        const leaParentOrganization = organizationEntity?.parentOrgRef;

        if (!jurisdiction) {
          setValue(
            'scope.jurisdiction',
            organizationEntity?.jurisdiction ||
              leaParentOrganization?.jurisdiction ||
              null
          );
        }
      }
    }, [
      organizationEntity, // schoolEntity,
      jurisdiction,
      setValue,
    ]);

    const organizationSeaChanged =
      item?.organizationSecondary !== organizationSecondary &&
      organizationSecondary !== organizationSecondaryPrev;
    const organizationLeaChanged =
      item?.organizationThird !== organizationThird &&
      organizationThird !== organizationThirdPrev;

    // GET SEA 2  (organizations2  "organizationSecondary")
    React.useEffect(() => {
      if (active && orgType === ORGANIZATION_TYPE_CODES.DEPARTMENT) {
        if (
          jurisdictionChanged ||
          (org2InputValuePrev !== org2InputValue &&
            organizationSecondaryEntityName !== org2InputValue)
        ) {
          if (
            item?.jurisdiction !== jurisdiction &&
            jurisdictionPrev &&
            jurisdiction &&
            org2InputValuePrev !== org2InputValue &&
            organizationSecondaryEntityName !== org2InputValue
          ) {
            setValue('scope.organizationSecondary', null);
            setValue('scope.organizationThird', null);
            setValue('scope.organization', null);
          }

          const data: Record<string, any> = {
            getWithTotal: true,
            filter: {
              organizationType: { eq: ORGANIZATION_TYPE_CODES.SEA },
            },
          };

          if (jurisdiction) {
            data.filter.and = [
              {
                jurisdiction: {
                  eq: jurisdiction,
                },
              },
            ];
          }

          if (org2InputValue) {
            data.name = org2InputValue;
            data.locale = locale;
          }

          getOrganizations(data, { secondary: true });
        }
      }
    }, [
      active,
      jurisdictionChanged,
      jurisdiction,
      jurisdictionPrev,
      orgType,
      org2InputValuePrev,
      org2InputValue,
      organizationSecondaryEntityName,
      organizationSecondary,
      organizationSecondaryPrev,
      item,
      locale,
      getOrganizations,
      setValue,
    ]);

    // GET LEA 3  (organizations3  "organizationThird")
    React.useEffect(() => {
      if (active && orgType === ORGANIZATION_TYPE_CODES.DEPARTMENT) {
        if (
          jurisdictionChanged ||
          organizationSeaChanged ||
          organizationLeaChanged
        ) {
          if (organizationSeaChanged && organizationSecondaryPrev) {
            setValue('scope.organizationThird', null);
            setValue('scope.organization', null);
          }
          if (
            item?.jurisdiction !== jurisdiction &&
            jurisdictionPrev &&
            jurisdiction &&
            org3InputValuePrev !== org3InputValue &&
            organizationThirdEntityName !== org3InputValue
          ) {
            setValue('scope.organizationSecondary', null);
            setValue('scope.organizationThird', null);
            setValue('scope.organization', null);
          }

          const data: Record<string, any> = {
            getWithTotal: true,
            filter: {
              organizationType: { eq: ORGANIZATION_TYPE_CODES.LEA },
            },
          };

          if (jurisdiction) {
            data.filter.and = [
              {
                jurisdiction: {
                  eq: jurisdiction,
                },
              },
            ];
          }

          if (organizationSecondary) {
            data.filter.parentOrg = {
              eq: organizationSecondary,
            };
          }

          if (org3InputValue) {
            data.name = org3InputValue;
            data.locale = locale;
          }

          getOrganizations(data, { third: true });
        }
      }
      // [org3InputValue, org3InputValuePrev, organizationLeaChanged, organizationThirdEntityName]
      // eslint-disable-next-line
    }, [
      active,
      jurisdictionChanged,
      orgType,
      org2InputValuePrev,
      org2InputValue,
      organizationSeaChanged,
      organizationSecondaryEntityName,
      organizationSecondary,
      organizationSecondaryPrev,
      jurisdiction,
      jurisdictionPrev,
      item,
      locale,
      getOrganizations,
      setValue,
    ]);

    // GET ORG
    React.useEffect(() => {
      const typeChanged = orgType !== orgTypePrev;

      const orgInputChanged =
        orgInputValuePrev !== orgInputValue &&
        organizationEntityName !== orgInputValue;

      if (
        active &&
        orgType &&
        (typeChanged || jurisdictionChanged || orgInputChanged)
      ) {
        if (!jurisdiction && organizationEntityJurId) {
          setValue('scope.jurisdiction', organizationEntityJurId || null);
        }
        if (
          (jurisdictionChanged && jurisdictionPrev && jurisdiction) ||
          (jurisdiction &&
            organizationEntityJurId &&
            organizationEntityJurId !== jurisdiction)
        ) {
          setValue('scope.organization', null);
        }

        const data: Record<string, any> = {
          populateParent: true,
          getWithTotal: true,
          // filter: { accessScope: { eq: orgAccessScope } },
          filter: { organizationType: { eq: orgType } },
        };

        if (jurisdiction) {
          data.filter.and = [
            {
              jurisdiction: {
                eq: jurisdiction,
              },
            },
          ];
        }

        if (organizationThird || organizationSecondary) {
          data.filter.parentOrg = {
            eq: organizationThird || organizationSecondary,
          };
        }

        if (orgInputValue) {
          data.name = orgInputValue;
          data.locale = locale;
        }

        if (onlyMyAccessByModule) {
          data.filterAccess = {};

          // data.filterAccess.accessScope = ACCESS_SCOPES.LEA;
          data.filterAccess.moduleCode = onlyMyAccessByModule;
        }

        getOrganizations(data);
      }
    }, [
      active,
      jurisdictionChanged,
      organizationThird,
      organizationSecondary,
      orgType,
      orgAccessScope,
      orgTypePrev,
      organizationEntityName,
      organizationEntityJurId,
      orgInputValue,
      orgInputValuePrev,
      onlyMyAccessByModule,
      jurisdiction,
      jurisdictionPrev,
      item,
      locale,
      getOrganizations,
      setValue,
    ]);

    const typesOptions = prepareOptions(organizationTypes, {
      locale,
      field: 'description',
      disabledFunc: (item) => {
        let disabled = item.disabled;
        if (onlyMyAccessByModule && typeof disabled === 'undefined') {
          disabled = true;
        }

        return disabled;
      },
    });
    const jurisdictionsOptions = prepareOptions(jurisdictions, {
      locale,
    });

    const organizationsOptions = React.useMemo(() => {
      const organizationsOptionsActual = isFetchingOrganizations
        ? []
        : prepareOptions(organizations, {
            locale,
            type: 'organization',
          });

      const isOrganizationExistsInActual =
        organizationStore &&
        organizationsOptionsActual.findIndex(
          (item) => item.value === organizationStore?._id
        ) > -1;

      const schoolOptionInitial =
        !isOrganizationExistsInActual && organizationStore
          ? prepareOptions([organizationStore], {
              locale,
              type: 'organization',
            })
          : [];

      return [...schoolOptionInitial, ...organizationsOptionsActual];
    }, [organizations, organizationStore, locale]);

    const organizations2WithCurrentEntity = [
      ...(organizations2 || []),
      organizationSecondaryEntity,
    ]
      .filter((item) => !!item)
      .filter((value, index, self) => {
        return self.findIndex((subItem) => subItem._id === value._id) === index;
      });
    const organizations2Options = organizations2WithCurrentEntity
      ? prepareOptions(organizations2WithCurrentEntity, {
          locale,
        })
      : [];

    const organizations3WithCurrentEntity = [
      ...(organizations3 || []),
      organizationSecondaryEntity,
    ]
      .filter((item) => !!item)
      .filter((value, index, self) => {
        return self.findIndex((subItem) => subItem._id === value._id) === index;
      });
    const organizations3Options = organizations3WithCurrentEntity
      ? prepareOptions(organizations3WithCurrentEntity, {
          locale,
        })
      : [];

    const formHelper = (field: string) => {
      return formErrorHelper(field, errors, serverErrors);
    };

    return (
      <>
        <AnvSelect
          name={'scope.orgType'}
          control={control}
          required
          label={
            <FormattedMessage
              id="APP.COMMON.LABEL.ORGANIZATION.EDUCATIONAL_ORGANIZATION_TYPE"
              defaultMessage="Educational Organization Type"
            />
          }
          options={typesOptions}
          helperText={formHelper('scope.orgType')}
          error={!!formHelper('scope.orgType')}
          value={orgType}
        />

        <AnvSelect
          displayEmpty
          name={'scope.jurisdiction'}
          control={control}
          label={
            <FormattedMessage
              id="APP.COMMON.LABEL.JURISDICTION"
              defaultMessage="Jurisdiction"
            />
          }
          options={jurisdictionsOptions}
          helperText={formHelper('scope.jurisdiction')}
          error={!!formHelper('scope.jurisdiction')}
          value={jurisdiction}
        />

        {orgType === ORGANIZATION_TYPE_CODES.DEPARTMENT && (
          <>
            <AnvAutocomplete2
              total={organizationsTotal2}
              options={organizations2Options}
              loading={isFetchingOrganizations}
              label={
                <FormattedMessage
                  id="APP.COMMON.LABEL.ORGANIZATION.SEA"
                  defaultMessage="SEA"
                />
              }
              onInputChange={(event, newInputValue) => {
                setOrg2InputValue(newInputValue);
              }}
              control={control}
              name={'scope.organizationSecondary'}
              helperText={formHelper('scope.organizationSecondary')}
              error={!!formHelper('scope.organizationSecondary')}
            />

            <AnvAutocomplete2
              total={organizationsTotal3}
              options={organizations3Options}
              loading={isFetchingOrganizations}
              label={
                <FormattedMessage
                  id="APP.COMMON.LABEL.ORGANIZATION.LEA"
                  defaultMessage="LEA"
                />
              }
              onInputChange={(event, newInputValue) => {
                setOrg3InputValue(newInputValue);
              }}
              control={control}
              name={'scope.organizationThird'}
              helperText={formHelper('scope.organizationThird')}
              error={!!formHelper('scope.organizationThird')}
              value={organizationThird}
            />
          </>
        )}

        <AnvAutocomplete2
          total={organizationsTotal}
          required={!!organizationRequired}
          options={organizationsOptions}
          loading={isFetchingOrganizations}
          label={
            <FormattedMessage
              id="APP.COMMON.LABEL.ORGANIZATION"
              defaultMessage="Organization"
            />
          }
          onInputChange={(event, newInputValue) => {
            setOrgInputValue(newInputValue);
          }}
          control={control}
          name={'scope.organization'}
          helperText={formHelper('scope.organization')}
          error={!!formHelper('scope.organization')}
        />
      </>
    );
  }
);

export default ModuleScopeFilterOrganization;
