export interface IJurisdiction {
  _id: string;
  code: string;
  name: string;
  countryRef?: string;
  createdAt: string;
  updatedAt: string;
}

export class Jurisdiction {
  public _id: string;
  public name: Record<string, any>;
  public code: string;
  public countryRef?: string;

  public createdAt: Date | string;
  public updatedAt: Date | string;

  constructor(item: any) {
    this._id = item._id;
    this.name = item.name;
    this.code = item.code;
    this.countryRef = item.countryRef;

    this.createdAt = item.createdAt;
    this.updatedAt = item.updatedAt;
  }
}
