import React from 'react';
import { Grid } from '@material-ui/core';
import clsx from 'clsx';

import { useStyles } from '../styles';

interface IProps {
  // total: number;
  title: JSX.Element | string;
  className?: string;
}

export const GroupHeaderAutocomplete: React.FC<IProps> = ({
  title,
  className = '',
  children,
}) => {
  const classes = useStyles();

  return (
    <Grid
      className={clsx(classes.wrapper, classes.autocompleteGroupHeader, {
        [className]: !!className,
      })}
      container
      spacing={2}
      justify={'space-between'}
    >
      <Grid item>{title}</Grid>
      <Grid item>{children}</Grid>
    </Grid>
  );
};
