import React from 'react';
import { HeaderGroup } from 'react-table';

import clsx from 'clsx';
import { useIntl } from 'react-intl';
import { TextField } from '@material-ui/core';

import {
  AnvSelect,
  ISelectOption,
} from 'src/components/shared/form-controls/anv-select';
import { AnvDatepicker } from 'src/components/shared/form-controls/anv-datepicker/anv-datepicker';

import { FILTER_TYPES } from 'src/constants';

import { useStyles } from './styles';

export interface CustomHeaderGroup extends HeaderGroup {
  accessor?: () => string;

  filterable?: boolean;
  filterType?: string;
  filterOptions?: ISelectOption[];
  sortable?: boolean;
  headerString?: string;
}

interface IProps {
  column: CustomHeaderGroup;
  handleChangeFilter: (value: any) => void;
  handleChangePhone: (ev: any) => void;
  handleChangeFilterDate: (name: string, date: any) => void;
  value: any;
}

export const AnvTableHeadFilter: React.FC<IProps> = ({
  // name,
  column,
  value,
  handleChangeFilter,
  handleChangeFilterDate,
  handleChangePhone,
}) => {
  const classes = useStyles();
  const { formatMessage } = useIntl();

  // let name = column?.accessor?.() || column?.accessor || column?.id;
  let name = column?.id;

  if (
    (column?.filterType === FILTER_TYPES.boolean && column?.filterOptions) ||
    column?.filterType === FILTER_TYPES.select
  ) {
    return (
      <AnvSelect
        name={column.id}
        className={clsx('filterInput', 'withoutLabel', classes.filterControl)}
        disabled={
          !(column?.filterOptions?.length || column?.filterOptions?.length)
        }
        options={column.filterOptions || []}
        aria-label={`${formatMessage({
          id: 'APP.COMMON.LABEL.FILTER_BY',
          defaultMessage: 'Filter by',
        })} ${column.headerString || ''}`}
        onChange={handleChangeFilter}
        value={value}
        displayEmpty
      />
    );
  }

  if (column?.filterType === FILTER_TYPES.date) {
    return (
      <AnvDatepicker
        name={name}
        className={clsx('filterInput', 'withoutLabel', classes.filterControl)}
        value={value}
        onChange={(date) => handleChangeFilterDate(name, date)}
        aria-label={`${formatMessage({
          id: 'APP.COMMON.LABEL.FILTER_BY',
          defaultMessage: 'Filter by',
        })} ${column.headerString || ''}`}
        placeholder={column.headerString || ''}
        // clearable={true}
        // clearLabel={formatMessage({
        //   id: "DATEPICKER.CLEAR",
        //   defaultMessage: "Clear",
        // })}
      />
    );
  }

  return (
    <TextField
      name={name}
      className={clsx('withoutLabel', 'filterInput', classes.filterControl)}
      size={'small'}
      aria-label={`${formatMessage({
        id: 'APP.COMMON.LABEL.FILTER_BY',
        defaultMessage: 'Filter by',
      })} ${column.headerString || ''}`}
      placeholder={column.headerString || ''}
      variant={'filled'}
      onChange={handleChangeFilter}
      value={value}
    />
  );
};
