import React from 'react';

import { Controller } from 'react-hook-form';
import { Grid, TextField } from '@material-ui/core';
import { SelectProps } from '@material-ui/core/Select';
import Autocomplete, {
  AutocompleteRenderGroupParams,
} from '@material-ui/lab/Autocomplete';

import LabelLoading from '../LabelLoading';
import { ISelectOption } from '../anv-select';
import {
  AutocompleteTotalContext,
  PaperTotalAutocomplete,
} from './parts/PaperTotalAutocomplete';

interface IProps extends SelectProps {
  name: string;
  control: any;
  helperText?: string;
  options: ISelectOption[];
  loading?: boolean;
  multiple?: boolean;
  onInputChange?: (event: any, newValue: any) => void;
  handleHomeEndKeys?: boolean;
  disableCloseOnSelect?: boolean;
  freeSolo?: boolean;
  filterOptions?: any;
  open?: boolean;
  total?: number;
  renderTags?: any;
  groupBy?: (option: ISelectOption) => string;
  renderGroup?: (params: AutocompleteRenderGroupParams) => React.ReactNode;
}

interface IGetInputValue {
  multiple?: boolean;
  options: ISelectOption[];
  valueProps: string | Record<string, any> | Record<string, any>[];
  valueParams: string | Record<string, any> | Record<string, any>[] | any;
}
const getInputValue = ({
  multiple = false,
  options,
  valueProps,
  valueParams,
}: IGetInputValue) => {
  const defaultValue = multiple ? [] : '';

  // todo array
  return (
    (!multiple &&
      !valueParams &&
      valueProps &&
      options.find((option) => option.value === valueProps)?.label) ||
    valueParams ||
    defaultValue
  );
};

export const AnvAutocomplete2: React.FC<IProps> = ({
  name,
  label,
  control,
  error,
  helperText,
  autoFocus,
  className = '',
  options,
  required,
  disabled = false,
  value,
  loading = false,
  multiple = false,
  disableCloseOnSelect = false,
  freeSolo = false,
  onInputChange,
  handleHomeEndKeys = false,
  open = undefined,
  filterOptions,
  total,
  groupBy,
  renderGroup,
  renderTags,
  ...rest
}) => {
  const getOpObj = (option: any) => {
    if (!option?.value) option = options.find((op) => op.value === option);
    return option;
  };

  return (
    <AutocompleteTotalContext.Provider value={total}>
      <Controller
        render={(props) => (
          <Autocomplete
            {...props}
            open={open}
            renderGroup={renderGroup}
            disableCloseOnSelect={disableCloseOnSelect}
            freeSolo={freeSolo}
            groupBy={groupBy}
            multiple={multiple}
            renderTags={renderTags}
            size={'small'}
            options={options}
            filterOptions={filterOptions}
            getOptionLabel={(option) => {
              return getOpObj(option)?.label || '';
            }}
            getOptionDisabled={(option) => !!option.disabled}
            getOptionSelected={(option, value) => {
              return option.value === getOpObj(value)?.value;
            }}
            PaperComponent={PaperTotalAutocomplete}
            onInputChange={onInputChange}
            renderOption={(option) => {
              return (
                <Grid
                  container
                  direction="row"
                  alignItems="center"
                  justify="space-between"
                  xs={12}
                >
                  <Grid item xs={option.rightCaption ? 9 : 12}>
                    {option.label}
                  </Grid>
                  <Grid item xs={12}>
                    {option.bottomCaption}
                  </Grid>

                  {!!option.rightCaption && (
                    <Grid item>{option.rightCaption || ''}</Grid>
                  )}
                </Grid>
              );
            }}
            renderInput={(params) => (
              <>
                <TextField
                  {...params}
                  required={required}
                  name={name}
                  fullWidth
                  label={
                    label && (
                      <>
                        {label}
                        <LabelLoading isLoading={loading} />
                      </>
                    )
                  }
                  inputProps={{
                    ...params.inputProps,
                    value: getInputValue({
                      multiple,
                      options,
                      valueProps: props.value,
                      // @ts-ignore
                      valueParams: params?.inputProps?.value,
                    }),
                  }}
                  variant="filled"
                  helperText={helperText}
                  error={error}
                />
              </>
            )}
            onChange={(_, data: any) => {
              if (multiple) {
                if (multiple && freeSolo) {
                  const newData = data.map((item: any) => {
                    if (typeof item === 'string') {
                      return {
                        label: item,
                        active: undefined,
                        disabled: false,
                        type: 'All Tags',
                        value: item,
                      };
                    }
                    //Проверка для того что бы убрать слово 'Add' при добавлении через пользовательский клик
                    if (item.label.includes('Add')) {
                      return {
                        ...item,
                        label: item.label.slice(3),
                      };
                    }
                    return item;
                  });
                  return props.onChange(newData);
                }

                return props.onChange(data);
              }
              // @ts-ignore
              // console.log('onChange-data?.value---', data, data?.value);
              // @ts-ignore
              return props.onChange(data?.value);
            }}
            handleHomeEndKeys={handleHomeEndKeys}
          />
        )}
        name={name}
        control={control}
      />
    </AutocompleteTotalContext.Provider>
  );
};
