import React from 'react';
import clsx from 'clsx';

import { Button, ButtonProps } from '@material-ui/core';

import { useStyles } from './styles';

const colorMuiProp = ['inherit', 'primary', 'secondary', 'default'];

interface IProps extends ButtonProps {
  customColor?:
    | 'inherit'
    | 'muted'
    | 'primary'
    | 'secondary'
    | 'default'
    | 'warning'
    | 'error'
    | 'success'
    | 'info'
    | 'white';
  smallPadding?: boolean;
  inactive?: boolean;
}

export const AnvButton: React.FC<IProps> = ({
  children,
  customColor = 'default',
  className = '',
  smallPadding = false,
  inactive = false,
  ...props
}) => {
  const classes = useStyles();

  let classNameCustom;

  if (colorMuiProp.indexOf(customColor) === -1) {
    classNameCustom = clsx(classes.common, {
      // @ts-ignore
      [classes[customColor]]: !!classes[customColor],
    });
  }

  return (
    <Button
      className={clsx(classes.common, classNameCustom, {
        [className]: !!className,
        [classes.smallPadding]: smallPadding,
        [classes.inactive]: inactive,
      })}
      color={'primary'}
      {...props}
    >
      {children}
    </Button>
  );
};

export default AnvButton;
