import React from 'react';

import { observer } from 'mobx-react-lite';
import { useFormContext, useWatch } from 'react-hook-form';
import { FormattedMessage, useIntl } from 'react-intl';
import clsx from 'clsx';

import {
  CircularProgress,
  Collapse,
  Divider,
  Grid,
  Menu,
  MenuItem,
} from '@material-ui/core';
import { ExpandMore } from '@material-ui/icons';

import { ITool } from 'src/models';

import { AnvSelect } from 'src/components/shared/form-controls/anv-select';

import ScopeFilterSchool from './form-parts/ScopeFormFilterSchool';
import ScopeFilterOrganization from './form-parts/ScopeFormFilterOrganization';

import { ACCESS_SCOPES } from 'src/constants';
import {
  getItemById,
  getNameByLangNullable,
  prepareOptions,
} from '../../utils';
import { AnvButtonGroup } from '../shared/anv-button-group';
import { AnvButton } from '../shared/anv-button';
import { AnvIcon } from '../shared/icons';
import { useModal } from '../../utils/hooks';
import { GroupHeaderAutocomplete } from '../shared/form-controls/anv-autocomplete-2/parts/GroupHeader';
import { AnvAccessHistoryModal } from '../scope-form/history-modal';
import { FILTER_SETTINGS } from './constants-filter';

import { IGetAccessHistoryParams } from '../../stores/_helpers/interfaces';

import { useStyles } from './styles';

interface IMetaStores {
  jurisdictionStore: Record<string, any>;
  organizationTypesStore: Record<string, any>;
  organizationsStore: Record<string, any>;
  tagsStore: Record<string, any>;
  schoolsStore: Record<string, any>;
  accessHistoryStore: Record<string, any>;
}
interface IProps {
  metaStores?: IMetaStores;
  isShowActions?: boolean;

  isShowMyData?: boolean;

  displayEmptyTool?: boolean;
  displayEmptyModule?: boolean;
  displayEmptyAccessScope?: boolean;

  withHistory?: boolean;

  particularScope?: string;
  particularAccessScope?: string;
  particularTool?: string;
  particularModule?: string;

  hideTool?: boolean;
  hideModule?: boolean;
  hideTags?: boolean;
  hideAccessScope?: boolean;

  schoolRequired?: boolean;
  organizationRequired?: boolean;

  onlyMyAccessByModule?: string;

  tools?: ITool[] | null;
  modules?: Record<string, any>[] | null;
  jurisdictions?: Record<string, any>[] | null;
  organizations?: Record<string, any>[] | null;
  schools?: Record<string, any>[] | null;
  accessScopes?: Record<string, any>[] | null;

  accessScopeStore?: Record<string, any> | null;

  moduleSettings?: Record<string, any> | null;

  existsData?: Record<string, any> | null;
  serverErrors?: Record<string, any> | null;
}

export const defaultMetaStore = {
  jurisdictionStore: {
    getJurisdictions: (arg: any, arg2?: any) => ({}),
    data: null,
    isFetching: false,
    isLoaded: false,
  },
  accessHistoryStore: {
    getAccessHistory: (arg: any, arg2?: any) => ({}),
    data: null,
    isFetching: false,
    isLoaded: false,
  },
  organizationTypesStore: {
    getOrganizationTypes: () => ({}),
    data: null,
    isFetching: false,
    isLoaded: false,
  },
  organizationsStore: {
    getOrganizations: (arg: any, arg2?: any) => ({}),
    getOrganization: (arg: any, arg2?: any) => ({}),
    data: null,
    data2: null,
    organizationsMap: {},
    isFetching: false,
    isLoaded: false,
    total: 0,
    total2: 0,
  },
  tagsStore: {
    getTags: (arg: any, arg2?: any) => ({}),
    data: null,
    isFetching: false,
    isLoaded: false,
  },
  schoolsStore: {
    getSchool: (arg: any, arg2?: any) => ({}),
    getSchools: (arg: any, arg2?: any) => ({}),
    schoolsMap: {},
    data: null,
    isFetching: false,
    isLoaded: false,
    total: 0,
  },
};
export const ScopeFormContext = React.createContext<IProps>({
  metaStores: defaultMetaStore,
});

export const ScopeFormFilter: React.FC<IProps> = observer(
  ({
    metaStores,
    particularModule,
    particularTool,
    particularAccessScope,
    hideTool,
    hideModule,
    hideTags,
    hideAccessScope,
    withHistory = false,

    schoolRequired,
    organizationRequired,

    displayEmptyTool = true,
    displayEmptyModule = true,
    displayEmptyAccessScope = true,

    onlyMyAccessByModule,

    tools = [],
    modules = [],
    organizations = [],
    jurisdictions = [],
    schools = [],
    // accessScopes = [],

    moduleSettings,

    accessScopeStore,

    existsData,
    serverErrors,
    children,
  }) => {
    const { locale } = useIntl();
    const { setValue, control, register } = useFormContext();

    React.useEffect(() => {
      register('accessScope');
    });

    const classes = useStyles();

    const moduleWatcher = useWatch({
      control,
      name: 'module',
      defaultValue: '',
    });

    const orgTypeWatcher = useWatch({
      control,
      name: 'scope.orgType',
      defaultValue: '',
    });

    //////////////////
    const { getAccessScopes } = accessScopeStore || {};
    const {
      organizationTypesStore: { data: organizationTypes },
      accessHistoryStore: {
        getAccessHistory,
        data: accessHistory = [],
        isFetching: isFetchingAccessHistory,
      },
    } = metaStores || defaultMetaStore;

    const historyActiveItems = accessHistory?.filter(
      (historyItem: Record<string, any>) => historyItem?.active
    );

    React.useEffect(() => {
      if (getAccessScopes) {
        const filterAccess: Record<string, any> = {};

        if (onlyMyAccessByModule) {
          filterAccess.moduleCode = onlyMyAccessByModule;
          filterAccess.toolCode = particularTool;
        }

        getAccessScopes({ filterAccess });
      }
    }, [particularTool, onlyMyAccessByModule, getAccessScopes]);

    /////// HISTORY //////
    const { handleToggleModal: handleToggleHistory, isOpen: historyOpen } =
      useModal();

    const [anchorHistory, setAnchorHistory] =
      React.useState<HTMLButtonElement | null>(null);

    const handleClickHistory = (event: React.MouseEvent<HTMLButtonElement>) => {
      setAnchorHistory(event.currentTarget);
    };

    const handleCloseHistory = () => {
      setAnchorHistory(null);
    };

    const handleMenuItemHistoryClick = (item: Record<string, any>) => () => {
      handleCloseHistory();

      if (item.school) {
        setValue('scope.jurisdiction', item.schoolRef?.jurisdiction || null);
        setValue('scope.tag', '');
        setValue('scope.school', item.school);
      } else if (item.organization) {
        if (item.organizationRef?.organizationType?.accessScope) {
          setValue(
            'accessScope',
            item.organizationRef?.organizationType?.accessScope
          );
        }
        setValue('scope.orgType', item.organizationRef?.organizationType?._id);

        setTimeout(() => {
          setValue(
            'scope.jurisdiction',
            item.organizationRef?.jurisdiction || null
          );
          setValue('scope.organization', item.organization);
        }, 0);
      }
    };

    const organizationTypeEntity =
      orgTypeWatcher && getItemById(organizationTypes, orgTypeWatcher);
    const orgAccessScope =
      organizationTypeEntity && organizationTypeEntity?.accessScope;

    let actualOrgAccessScope =
      orgAccessScope || existsData?.accessScope || null;
    actualOrgAccessScope =
      actualOrgAccessScope !== ACCESS_SCOPES.SCHOOL
        ? actualOrgAccessScope
        : null;

    const schoolFilterAvailable =
      !moduleSettings?.[FILTER_SETTINGS.FILTER_BY_SCHOOL] ||
      moduleSettings?.[FILTER_SETTINGS.FILTER_BY_SCHOOL] === 'true';

    const organizationFilterAvailable =
      !moduleSettings?.[FILTER_SETTINGS.FILTER_BY_ORGANIZATION] ||
      moduleSettings?.[FILTER_SETTINGS.FILTER_BY_ORGANIZATION] === 'true';

    // const organizationFilterParticular =
    //   moduleSettings?.[FILTER_SETTINGS.FILTER_PARTICULAR_ORGANIZATION] ===
    //   'true';

    const isTabsTypeAvailable =
      schoolFilterAvailable && organizationFilterAvailable;

    const [organizationTab, setOrganizationTab] = React.useState(
      (existsData?.accessScope &&
        existsData?.accessScope !== ACCESS_SCOPES.SCHOOL) ||
        !schoolFilterAvailable
    );
    const handleChangeTab =
      (isOrganization = false) =>
      () => {
        setOrganizationTab(isOrganization);

        if (isOrganization && !organizationTab) {
          setValue('accessScope', actualOrgAccessScope || '');

          setValue('scope.jurisdiction', null);
          setValue('scope.organizationThird', null);
          setValue('scope.organizationSecondary', null);
          setValue('scope.organization', null);
        }

        if (!isOrganization && organizationTab) {
          setValue('accessScope', ACCESS_SCOPES.SCHOOL);
          setValue('scope.orgType', null);

          setValue('scope.jurisdiction', null);
          setValue('scope.tags', null);
          setValue('scope.school', null);
        }
      };

    React.useEffect(() => {
      if (withHistory && (moduleWatcher || particularModule)) {
        const payload: IGetAccessHistoryParams = {
          moduleCode: moduleWatcher || particularModule,
        };

        if (!organizationTab) {
          payload.accessScope = ACCESS_SCOPES.SCHOOL;
        }

        if (organizationTab) {
          payload.isOrganization = true;
        }

        getAccessHistory(payload);
      }

      // const ready =
      //   (accessScope !== ACCESS_SCOPES.SCHOOL && organizationTab) ||
      //   (accessScope === ACCESS_SCOPES.SCHOOL && !organizationTab);
      // if (
      //   ready &&
      //   withHistory &&
      //   (moduleWatcher || particularModule) &&
      //   (accessScope || organizationTab)
      // ) {
      //   const payload: IGetAccessHistoryParams = {
      //     moduleCode: moduleWatcher || particularModule,
      //   };
      //
      //   if (accessScope && !organizationTab) {
      //     payload.accessScope = accessScope;
      //   }
      //
      //   if (organizationTab) {
      //     payload.isOrganization = true;
      //   }
      //
      //   getAccessHistory(payload);
      // }
    }, [
      organizationTab,
      moduleWatcher,
      particularModule,
      // accessScope,
      withHistory,
      getAccessHistory,
    ]);
    ///// history end

    const toolsOptions =
      !hideTool && tools
        ? prepareOptions(tools, {
            locale,
            customId: 'code',
          })
        : [];
    const moduleOptions =
      !hideModule && modules
        ? prepareOptions(
            modules?.filter((item) => item?.restricted),
            {
              locale,
              fallbackFunc: (item) => item?.moduleCode,
            }
          )
        : [];

    return (
      <ScopeFormContext.Provider
        value={{
          jurisdictions,
          existsData,
          hideTags,
          onlyMyAccessByModule,
          metaStores,
          particularModule,
          particularTool,

          withHistory,
          serverErrors,

          schoolRequired,
          organizationRequired,
        }}
      >
        <div>
          {!hideTool && (
            <AnvSelect
              required
              displayEmpty={displayEmptyTool}
              name={'tool'}
              disabled={!!particularTool}
              label={
                <FormattedMessage
                  id="LAUNCHPAD.TOOL.LABEL.TOOL"
                  defaultMessage="Tool"
                />
              }
              options={toolsOptions}
              defaultValue={particularTool}
            />
          )}

          {!hideModule && (
            <AnvSelect
              required
              displayEmpty={displayEmptyModule}
              name={'module'}
              label={
                <FormattedMessage
                  id="COMMON.ACCESS.LABEL.MODULE"
                  defaultMessage="Module"
                />
              }
              disabled={!!particularModule}
              options={moduleOptions}
              value={particularModule}
            />
          )}

          <Grid
            container
            justify={'space-between'}
            alignItems={'center'}
            className={classes.mainTabs}
          >
            <Grid item>
              {isTabsTypeAvailable && !particularAccessScope && (
                <AnvButtonGroup>
                  <AnvButton
                    inactive={!organizationTab}
                    variant={organizationTab ? 'outlined' : 'contained'}
                    onClick={handleChangeTab(false)}
                  >
                    <FormattedMessage
                      id="APP.COMMON.FILTER.HISTORY.BY_SCHOOL"
                      defaultMessage="By School"
                    />
                  </AnvButton>
                  <AnvButton
                    inactive={organizationTab}
                    variant={!organizationTab ? 'outlined' : 'contained'}
                    onClick={handleChangeTab(true)}
                  >
                    <FormattedMessage
                      id="APP.COMMON.FILTER.HISTORY.BY_ORGANIZATION"
                      defaultMessage="By Organization"
                    />
                  </AnvButton>
                </AnvButtonGroup>
              )}
            </Grid>

            <Grid item>
              {!!historyActiveItems?.length && (
                <>
                  <AnvButton
                    disabled={isFetchingAccessHistory}
                    size={'small'}
                    variant={'text'}
                    color={'primary'}
                    startIcon={<AnvIcon icon={'history'} size={16} />}
                    endIcon={
                      isFetchingAccessHistory ? (
                        <CircularProgress size={12} />
                      ) : (
                        <ExpandMore
                          className={clsx(classes.expandMore, {
                            [classes.expandRotate]: !!anchorHistory,
                          })}
                        />
                      )
                    }
                    onClick={handleClickHistory}
                  >
                    <FormattedMessage
                      id="APP.COMMON.FILTER.HISTORY.BUTTON"
                      defaultMessage="Recently Viewed"
                    />
                  </AnvButton>

                  <Menu
                    id="lock-menu"
                    open={!!anchorHistory}
                    anchorEl={anchorHistory}
                    onClose={handleCloseHistory}
                    keepMounted
                    anchorOrigin={{
                      vertical: 'bottom',
                      horizontal: 'right',
                    }}
                    transformOrigin={{
                      vertical: 'top',
                      horizontal: 'right',
                    }}
                  >
                    <GroupHeaderAutocomplete
                      title={
                        <FormattedMessage
                          id={'APP.COMMON.FILTER.HISTORY.HEADER'}
                          defaultMessage={'Recently viewed'}
                        />
                      }
                    >
                      <AnvButton
                        className={classes.autocompleteHeaderButton}
                        variant={'text'}
                        color={'primary'}
                        onClick={handleToggleHistory}
                      >
                        <FormattedMessage
                          id="APP.COMMON.FILTER.ARCHIVE"
                          defaultMessage="Archive"
                        />
                      </AnvButton>
                    </GroupHeaderAutocomplete>

                    <Divider />

                    {historyActiveItems.map((item: Record<string, any>) => {
                      const name =
                        getNameByLangNullable(
                          item?.organizationRef,
                          'name',
                          locale
                        ) ||
                        getNameByLangNullable(item?.schoolRef, 'name', locale);

                      if (!name) return null;

                      return (
                        <MenuItem
                          key={item?._id}
                          onClick={handleMenuItemHistoryClick(item)}
                        >
                          {name}
                        </MenuItem>
                      );
                    })}
                  </Menu>

                  <AnvAccessHistoryModal
                    handleToggle={handleToggleHistory}
                    open={historyOpen}
                  />
                </>
              )}
            </Grid>
          </Grid>

          {/* SCHOOL */}
          <Collapse in={!organizationTab}>
            <ScopeFilterSchool
              active={!organizationTab}
              jurisdictions={jurisdictions}
            />
            {/*{!organizationTab && (*/}
            {/*  <ScopeFormSchool jurisdictions={jurisdictions} />*/}
            {/*)}*/}
          </Collapse>

          {/* Org */}
          <Collapse in={organizationTab}>
            <ScopeFilterOrganization
              active={organizationTab}
              jurisdictions={jurisdictions}
            />
            {/*{organizationTab && <ScopeFormLEA jurisdictions={jurisdictions} />}*/}
          </Collapse>
        </div>
      </ScopeFormContext.Provider>
    );
  }
);
