import React, { useEffect } from 'react';
import isEqual from 'lodash/isEqual';

import { observer } from 'mobx-react-lite';
import { FormattedMessage, useIntl } from 'react-intl';
import { useFormContext, useWatch } from 'react-hook-form';

import { IRequestAccessModuleScope } from 'src/models/requests';

import { AnvSelect } from 'src/components/shared/form-controls/anv-select';
import { AnvAutocomplete2 } from 'src/components/shared/form-controls/anv-autocomplete-2';

import {
  ACCESS_SCOPES,
  DEFAULT_LANGUAGE,
  INPUT_THROTTLE_TIME,
} from 'src/constants';
import { defaultMetaStore, ScopeFormContext } from '../index';

import { usePrevious } from 'src/hooks';
import { useDebounce } from 'src/utils/hooks';

import {
  formErrorHelper,
  getItemById,
  getNameByLangNullable,
  prepareOptions,
} from 'src/utils';

import { useStyles } from '../styles';

interface IProps {
  jurisdictions?: any;
  active?: boolean;
}

const ModuleRequestScopeSchool: React.FC<IProps> = observer(
  ({ active, jurisdictions }) => {
    const classes = useStyles();
    const { control, errors, setValue, getValues } = useFormContext();

    const {
      existsData: item,
      hideTags,
      onlyMyAccessByModule,
      metaStores = defaultMetaStore,
      serverErrors = {},
      schoolRequired,
    } = React.useContext(ScopeFormContext) || {};

    const { locale } = useIntl();

    const [schoolInputValue, setSchoolInputValue] = React.useState('');
    const schoolInputValueDef = useDebounce(
      schoolInputValue,
      INPUT_THROTTLE_TIME
    );

    // const [orgInputValue, setOrgInputValue] = React.useState('');
    // const orgInputValueDef = useDebounce(orgInputValue, INPUT_THROTTLE_TIME);

    const {
      jurisdictionStore: { isFetching: isFetchingJurisdictions },
      // organizationsStore: {
      //   getOrganization,
      //   getOrganizations,
      //   data: organizations,
      //   // organizationsMap,
      //   total: totalOrganizations,
      //
      //   isFetching: isFetchingOrganizations,
      // },
      tagsStore: {
        getTags,
        total: totalTags,
        data: tagsEntities,
        isFetching: isFetchingTags,
        isLoaded: isLoadedTags,
      },
      schoolsStore: {
        getSchool,
        getSchools,
        total: totalSchools,
        schoolsMap,
        data: schools,
        isFetching: isFetchingSchools,
      },
      accessHistoryStore: { data: accessHistory = [] },
    } = metaStores;

    const scope: IRequestAccessModuleScope = useWatch({
      control,
      name: 'scope',
      defaultValue: item
        ? item?.scope
        : {
            jurisdiction: null,
            organization: null,
            school: null,
          },
    });
    const tag = useWatch({
      control,
      name: 'scope.tag',
      defaultValue: item?.tag || '',
    });

    const [initialSchool] = React.useState(item?.scope?.school);
    // const [initialOrganization] = React.useState(item?.scope?.organization);

    React.useEffect(() => {
      if (initialSchool) {
        getSchool({ id: initialSchool });
      }
    }, [initialSchool, getSchool]);

    const { jurisdiction, school } = scope;

    // const tagsStringIds = (
    //   tags.map((tagOption) => tagOption?.value) || []
    // ).toString();

    useEffect(() => {
      if (!isLoadedTags) {
        getTags({ getWithTotal: true });
      }
    }, [isLoadedTags, getTags]);

    ///////////////////////////////////////////////////////////////
    const entitiesAll = React.useMemo(() => {
      const array: Record<string, any>[] = [];

      if (schools) {
        array.push(...(schools || []));
      }

      if (accessHistory) {
        array.push(
          ...(accessHistory
            .map(
              (historyItem: Record<string, any>) =>
                historyItem?.schoolRef /*|| historyItem?.organizationRef*/
            )
            .filter((item: any) => !!item) || [])
        );
      }

      return array;
    }, [schools, accessHistory]);

    const schoolStore =
      school &&
      (schoolsMap?.[school] ||
        entitiesAll?.find((item) => item._id === school));

    const schoolEntity = getItemById(entitiesAll || [], school);
    const schoolEntityName = getNameByLangNullable(
      schoolEntity,
      'name',
      locale
    );
    const schoolEntityJur = schoolEntity?.jurisdiction;

    // Set values from school ▲
    React.useEffect(() => {
      if (schoolEntity) {
        const schoolOrganizations = schoolEntity?.organizationsRefs;
        let selectedOrg = schoolOrganizations?.[0]?._id;

        if (!jurisdiction && selectedOrg?.jurisdiction) {
          setValue('scope.jurisdiction', selectedOrg?.jurisdiction || '');
        }
      }
    }, [schoolEntity, jurisdiction, setValue]);

    const schoolInputValuePrev = usePrevious(schoolInputValueDef);
    const tagPrev = usePrevious(tag);
    const jurisdictionPrev = usePrevious(jurisdiction);

    const isTagChangedFromItem = !isEqual(item?.tag, tag);

    /** Get SCHOOLS */
    React.useEffect(() => {
      const jurisdictionChanged =
        jurisdictionPrev !== jurisdiction &&
        schoolEntityJur !== jurisdiction &&
        !(!jurisdiction && !jurisdictionPrev && !schoolEntityJur);

      const tagsChanged = tag !== tagPrev && isTagChangedFromItem;

      if (
        active &&
        (!jurisdiction || schoolEntityJur !== jurisdiction) &&
        schoolEntityJur
      ) {
        setValue('scope.jurisdiction', schoolEntityJur || '');
      }

      if (
        active &&
        (jurisdictionChanged ||
          tagsChanged ||
          (schoolInputValuePrev !== schoolInputValueDef &&
            schoolInputValueDef !== schoolEntityName))
      ) {
        // if (!jurisdiction && schoolEntityJur) {
        //   setValue('scope.jurisdiction', schoolEntityJur || '');
        // }
        if (
          (jurisdictionChanged && jurisdictionPrev && jurisdiction) ||
          (jurisdiction &&
            schoolEntity &&
            schoolEntity?.jurisdiction !== jurisdiction)
        ) {
          setValue('scope.school', '');
        }

        if (
          tag &&
          tagsChanged &&
          school &&
          schoolEntity &&
          schoolEntity?.tags?.indexOf(tag) === -1
        ) {
          setValue('scope.school', '');
        }

        const data: Record<string, any> = {
          filter: {},
          getWithTotal: true,
        };
        if (locale !== DEFAULT_LANGUAGE) {
          data.locale = locale;
        }

        if (jurisdiction) {
          if (!data.filter?.or) {
            data.filter.or = [];
          }

          data.filter.or.push({
            jurisdiction: {
              eq: jurisdiction,
            },
          });
        }

        if (schoolInputValueDef) {
          data.name = schoolInputValueDef;
          data.locale = locale;
        }
        if (onlyMyAccessByModule) {
          data.filterAccess = {};

          data.filterAccess.accessScope = ACCESS_SCOPES.SCHOOL;
          data.filterAccess.moduleCode = onlyMyAccessByModule;
        }

        // if (tagsStringIds && tagsStringIds.length) {
        //   data.filter.tags = {
        //     all: tagsStringIds.split(','),
        //   };
        // }
        if (tag) {
          data.filter.tags = {
            in: [tag],
          };
        }

        getSchools(data);
      }
      // schoolEntity -> organizations
      // eslint-disable-next-line
    }, [
      active,
      schoolEntityName,
      schoolEntityJur,
      schoolInputValueDef,
      schoolInputValuePrev,
      jurisdiction,
      jurisdictionPrev,
      tag,
      tagPrev,
      isTagChangedFromItem,
      item,
      onlyMyAccessByModule,
      locale,
      getSchools,
      setValue,
      getValues,
    ]);

    const jurisdictionsOptions = prepareOptions(jurisdictions, {
      locale,
    });

    const tagsOptions = isFetchingTags
      ? []
      : prepareOptions(tagsEntities, {
          locale,
        });

    const schoolsOptionsActual = isFetchingSchools
      ? []
      : prepareOptions(schools, {
          locale,
          type: 'school',
          captionBottom: true,
          captionFunc: ({ address }) =>
            prepareSchoolAddressCaption({ address, locale, classes }),
        });

    const schoolsOptions = React.useMemo(() => {
      const isSchoolExistsInActual =
        schoolStore &&
        schoolsOptionsActual.findIndex(
          (item) => item.value === schoolStore?._id
        ) > -1;

      const schoolOptionInitial =
        !isSchoolExistsInActual && schoolStore
          ? prepareOptions([schoolStore], {
              locale,
              type: 'school',
              captionBottom: true,
              captionFunc: ({ address }) =>
                prepareSchoolAddressCaption({ address, locale, classes }),
            })
          : [];

      return [...schoolOptionInitial, ...schoolsOptionsActual];
      // [classes]
      // eslint-disable-next-line
    }, [schoolsOptionsActual, schoolStore, locale]);

    // SCHOOL: [
    //     'jurisdiction',
    //     'school is public',
    //     'organization type LEA (2)',
    //     'autocomplete - school name ',
    // ],

    const formHelper = (field: string) => {
      return formErrorHelper(field, errors, serverErrors);
    };

    return (
      <>
        <AnvSelect
          displayEmpty
          required={!schoolRequired}
          name={'scope.jurisdiction'}
          control={control}
          disabled={isFetchingJurisdictions}
          label={
            <FormattedMessage
              id="APP.COMMON.LABEL.JURISDICTION"
              defaultMessage="Jurisdiction"
            />
          }
          options={jurisdictionsOptions}
          helperText={formHelper('scope.jurisdiction')}
          error={!!formHelper('scope.jurisdiction')}
        />

        {/*<AnvAutocomplete2*/}
        {/*  total={totalOrganizations}*/}
        {/*  options={organizationsOptions}*/}
        {/*  loading={isFetchingOrganizations}*/}
        {/*  label={*/}
        {/*    <FormattedMessage*/}
        {/*      id="APP.COMMON.LABEL.ORGANIZATION.LEA"*/}
        {/*      defaultMessage="LEA Organization"*/}
        {/*    />*/}
        {/*  }*/}
        {/*  onInputChange={(event, newInputValue) => {*/}
        {/*    setOrgInputValue(newInputValue);*/}
        {/*  }}*/}
        {/*  onChange={() => {*/}
        {/*    setValue('scope.school', '');*/}
        {/*  }}*/}
        {/*  control={control}*/}
        {/*  name={'scope.organization'}*/}
        {/*  helperText={formHelper('scope.organization')}*/}
        {/*  error={!!formHelper('scope.organization')}*/}
        {/*  value={organization}*/}
        {/*/>*/}

        {!hideTags && (
          <AnvAutocomplete2
            total={totalTags || 0}
            // multiple
            options={tagsOptions}
            loading={isFetchingTags}
            label={
              <FormattedMessage
                id="APP.COMMON.LABEL.SYSTEM_LEVEL_TAG"
                defaultMessage="System Level Tag"
              />
            }
            control={control}
            name={'scope.tag'}
            helperText={formHelper('scope.tag')}
            error={!!formHelper('scope.tag')}
            value={tag}
          />
        )}

        <AnvAutocomplete2
          total={totalSchools}
          required={!!schoolRequired}
          options={schoolsOptions}
          loading={isFetchingSchools}
          label={
            <FormattedMessage
              id="APP.COMMON.LABEL.SCHOOL"
              defaultMessage="School"
            />
          }
          onInputChange={(event, newInputValue) => {
            setSchoolInputValue(newInputValue);
          }}
          control={control}
          name={'scope.school'}
          helperText={formHelper('scope.school')}
          error={!!formHelper('scope.school')}
          value={school}
        />
      </>
    );
  }
);

export default ModuleRequestScopeSchool;

const prepareSchoolAddressCaption = ({
  address = {},
  locale,
  classes,
}: any): string | JSX.Element => {
  const { stateRef: state, county, city } = address;
  const addressArr: string[] = [];
  const countyName = getNameByLangNullable(county, 'name', locale);
  const cityName = getNameByLangNullable(city, 'name', locale);

  if (state?.code) {
    addressArr.push(state.code);
  }
  if (countyName) {
    addressArr.push(countyName);
  }
  if (cityName) {
    addressArr.push(cityName);
  }

  return (
    <span className={classes.schoolAddressCaption}>
      {addressArr.join(', ')}
    </span>
  );
};
