export const ScopeForm: React.FC<IProps> = observer(
  ({
    metaStores,
    particularModule,
    particularTool,
    particularAccessScope,
    hideTool,
    hideModule,
    hideTags,
    hideAccessScope,
    withHistory = false,

    displayEmptyTool = true,
    displayEmptyModule = true,
    displayEmptyAccessScope = true,

    onlyMyAccessByModule,

    tools = [],
    modules = [],
    organizations = [],
    jurisdictions = [],
    schools = [],
    // accessScopes = [],

    accessScopeStore,

    existsData,
    serverErrors,
    children,
  }) => {
    const { locale } = useIntl();
    const { control, errors } = useFormContext();

    const moduleWatcher = useWatch({
      control,
      name: 'module',
      defaultValue: '',
    });
    const accessScopeWatcher = useWatch({
      control,
      name: 'accessScope',
      defaultValue: '',
    });

    const accessScope = particularAccessScope || accessScopeWatcher;
    //////////////////
    const { data: accessScopes, getAccessScopes } = accessScopeStore || {};

    React.useEffect(() => {
      if (getAccessScopes) {
        const filterAccess: Record<string, any> = {};

        if (onlyMyAccessByModule) {
          filterAccess.moduleCode = onlyMyAccessByModule;
          filterAccess.toolCode = particularTool;
        }

        getAccessScopes({ filterAccess });
      }
    }, [accessScope, particularTool, onlyMyAccessByModule, getAccessScopes]);

    /////// HISTORY //////
    const {
      accessHistoryStore: { getAccessHistory },
    } = metaStores || defaultMetaStore;

    React.useEffect(() => {
      if ((moduleWatcher || particularModule) && withHistory) {
        getAccessHistory({
          moduleCode: moduleWatcher || particularModule,
          accessScope: accessScope,
        });
      }
    }, [
      moduleWatcher,
      particularModule,
      accessScope,
      withHistory,
      getAccessHistory,
    ]);
    /////

    const formHelper = (field: string) => {
      return formErrorHelper(field, errors, serverErrors || {});
    };

    const toolsOptions =
      !hideTool && tools
        ? prepareOptions(tools, {
            locale,
            customId: 'code',
          })
        : [];
    const moduleOptions =
      !hideModule && modules
        ? prepareOptions(
            modules?.filter((item) => item?.restricted),
            {
              locale,
              fallbackFunc: (item) => item?.moduleCode,
            }
          )
        : [];
    const accessScopesOptions =
      !hideAccessScope && accessScopes
        ? prepareOptions(accessScopes, {
            field: 'description',
            locale,
            disabledFunc: (item) => item?.disabled,
          })
        : [];

    return (
      <ScopeFormContext.Provider
        value={{
          jurisdictions,
          existsData,
          hideTags,
          onlyMyAccessByModule,
          metaStores,
          particularModule,

          withHistory,
          serverErrors,
        }}
      >
        <div>
          {!hideTool && (
            <AnvSelect
              required
              displayEmpty={displayEmptyTool}
              name={'tool'}
              disabled={!!particularTool}
              label={
                <FormattedMessage
                  id="LAUNCHPAD.TOOL.LABEL.TOOL"
                  defaultMessage="Tool"
                />
              }
              options={toolsOptions}
              defaultValue={particularTool}
            />
          )}

          {!hideModule && (
            <AnvSelect
              required
              displayEmpty={displayEmptyModule}
              name={'module'}
              label={
                <FormattedMessage
                  id="COMMON.ACCESS.LABEL.MODULE"
                  defaultMessage="Module"
                />
              }
              disabled={!!particularModule}
              options={moduleOptions}
              value={particularModule}
            />
          )}

          {!hideAccessScope && (
            <AnvSelect
              required
              displayEmpty={displayEmptyAccessScope}
              name={'accessScope'}
              control={control}
              label={
                <FormattedMessage
                  id="COMMON.ACCESS.LABEL.ACCESS_SCOPE"
                  defaultMessage="Access Scope"
                />
              }
              options={accessScopesOptions}
              helperText={formHelper('accessScope')}
              error={!!formHelper('accessScope')}
            />
          )}

          {/* SCHOOL */}
          <Collapse in={accessScope === ACCESS_SCOPES.SCHOOL}>
            {accessScope === ACCESS_SCOPES.SCHOOL && (
              <ScopeFormSchool jurisdictions={jurisdictions} />
            )}
          </Collapse>

          {/* AUTHORIZER */}
          <Collapse in={accessScope === ACCESS_SCOPES.AUTHORIZER}>
            {accessScope === ACCESS_SCOPES.AUTHORIZER && (
              <ScopeFormAuthorizer jurisdictions={jurisdictions} />
            )}
          </Collapse>

          {/* EMO */}
          <Collapse in={accessScope === ACCESS_SCOPES.EMO}>
            {accessScope === ACCESS_SCOPES.EMO && <ScopeFormEMO />}
          </Collapse>

          {/* ESC */}
          <Collapse in={accessScope === ACCESS_SCOPES.ESC}>
            {accessScope === ACCESS_SCOPES.ESC && <ScopeFormESC />}
          </Collapse>
          {/* SEA */}
          <Collapse in={accessScope === ACCESS_SCOPES.SEA}>
            {accessScope === ACCESS_SCOPES.SEA && <ScopeFormSEA />}
          </Collapse>
          {/* LEA */}
          <Collapse in={accessScope === ACCESS_SCOPES.LEA}>
            {accessScope === ACCESS_SCOPES.LEA && (
              <ScopeFormLEA jurisdictions={jurisdictions} />
            )}
          </Collapse>
        </div>
      </ScopeFormContext.Provider>
    );
  }
);
