import React from 'react';
import { Tooltip, TooltipProps } from '@material-ui/core';

interface IProps extends TooltipProps {
  // children?: JSX.Element;
}

export const AnvTooltip: React.FC<IProps> = ({ title, children, ...rest }) => {
  if (!(children && title)) {
    return null;
  }

  return (
    <Tooltip title={title} placement="bottom-start" {...rest}>
      {children}
    </Tooltip>
  );
};

export default AnvTooltip;
