const passwordStrength = 'To weak password';

export const validatePassword = (value: string | null) => {
  if (!value) {
    return passwordStrength;
  }

  if (!/((?=.*[a-z])(?=.*[A-Z]))+/.test(value)) {
    return passwordStrength;
  }

  if (!/([^a-zA-Z]|[0-9])+/.test(value)) {
    return passwordStrength;
  }

  if (!/([@#$%?!*&.])+/.test(value)) {
    return passwordStrength;
  }

  if (value.length < 8) {
    return passwordStrength;
  }

  return true;
};
