import React from 'react';

import { Controller } from 'react-hook-form';
import { createStyles, makeStyles } from '@material-ui/core/styles';
import {
  KeyboardDatePicker,
  KeyboardDateTimePicker,
  KeyboardDateTimePickerProps,
} from '@material-ui/pickers';
import clsx from 'clsx';
import { Control } from 'react-hook-form/dist/types/form';
import { DATE_FORMATS } from '../../../../constants';

interface IProps extends Partial<KeyboardDateTimePickerProps> {
  name: string;
  control?: Control;
  helperText?: string;
  format?: string;
  disabled?: boolean;
  required?: boolean;
  fullWidth?: boolean;
  autoOk?: boolean;
  label?: any;
  className?: any;
  isTime?: boolean;
  // value: object | string | number | Date | null | undefined;
  // onChange: (date: MaterialUiPickersDate | null, value?: string | null) => void;
  // value?: any;
}

export const AnvDatepicker: React.FC<IProps> = ({
  name,
  label,
  className = '',
  required,
  disabled = false,
  control,
  format,
  fullWidth = true,
  autoOk = true,
  value,
  ref,
  isTime = false,
  ...rest
}) => {
  const classes = useStyles();

  const defaultFormat = isTime
    ? `${DATE_FORMATS.DEFAULT_FORMAT} | ${DATE_FORMATS.TIME}`
    : DATE_FORMATS.DEFAULT_FORMAT;
  const Component = isTime ? KeyboardDateTimePicker : KeyboardDatePicker;

  if (control) {
    return (
      <Controller
        as={
          // @ts-ignore
          <Component
            ampm={false}
            className={clsx(className, classes.root)}
            fullWidth={fullWidth}
            autoOk={autoOk}
            disabled={disabled}
            inputVariant="filled"
            variant="inline"
            format={format || defaultFormat}
            label={label}
            required={required}
            orientation={'portrait'}
            rightArrowButtonProps={{
              size: 'small',
            }}
            leftArrowButtonProps={{
              size: 'small',
            }}
            {...rest}
          />
        }
        control={control}
        name={name}
      />
    );
  }

  // @ts-ignore
  return (
    //  @ts-ignore
    <Component
      className={clsx(className, classes.root)}
      fullWidth={fullWidth}
      autoOk={autoOk}
      disabled={disabled}
      inputVariant="filled"
      variant="inline"
      format={format || defaultFormat}
      label={label}
      required={required}
      orientation={'portrait'}
      rightArrowButtonProps={{
        size: 'small',
      }}
      leftArrowButtonProps={{
        size: 'small',
      }}
      value={value}
      {...rest}
    />
  );
  // from example - https://codesandbox.io/s/react-hook-form-v6-controller-forked-fm9bi?file=/src/Mui.js:726-1321
};

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      '& .MuiFilledInput-adornedEnd': {
        '& .MuiInputBase-input.MuiFilledInput-input': {
          paddingRight: 0,
        },
        paddingRight: 0,
      },

      '& .MuiButtonBase-root': {
        padding: theme.spacing(1),
        marginRight: 0,
        marginLeft: theme.spacing(1),
      },
      '& .MuiButton-root.MuiButton-text': {
        padding: theme.spacing(1, 2),
        marginRight: 0,
        marginLeft: theme.spacing(1),
      },
    },
  })
);
